import React, { useState, useRef, useEffect } from "react";
import Loader from "../../common/loader/Loader";
import { ToastContainer } from "react-toastify";
import { usePayoutContext } from "./PayoutProvider";
import {
  Box,
  Button,
  Card,
  CardActionArea,
  TextField,
  CardContent,
  Grid,
  Dialog,
  Typography,
  IconButton,
  CircularProgress,
  Paper,
  Chip,
  MenuItem,
  Stack,
  Autocomplete,
} from "@mui/material";
import { CheckCircle } from "@mui/icons-material";
import ArrowBackIosNewIcon from "@mui/icons-material/ArrowBackIosNew";
import { bulkPayout, getAllBank } from "../../api/login/Login";
import toastMessage from "../../utils/toastMessage";

const CustomTextField = (props) => (
  <TextField
    variant="outlined"
    size="small"
    InputLabelProps={{
      shrink: true,
      //   style: { fontSize: '16px' },
    }}
    sx={{
      "& label.Mui-focused": {
        color: "var(--bs-nav)",
      },
      "& .MuiOutlinedInput-root": {
        height: 40,
        "& .MuiOutlinedInput-input": {
          py: 0,
        },
        "&:hover fieldset": { borderColor: "var(--bs-primary1)" },
        "&.Mui-focused fieldset": { borderColor: "var(--bs-primary1)" },
      },
    }}
    {...props}
  />
);

const CustomBox = ({ children }) => (
  <Box
    sx={{
      width: "100%",
      //   maxWidth: 800,
      mx: "auto",
      //   mt: 5,
      my: 1,
      px: 3,
      py: 4,
      pb: 6,
      //   bgcolor: '#F8F9FA',
      bgcolor: "white",
      borderRadius: 1,
      boxShadow: 0,
    }}
  >
    {children}
  </Box>
);

const BulkPayout = () => {
  const fileInputRef = useRef();
  const [uploading, setUploading] = useState(false);
  const [uploadResult, setUploadResult] = useState(null);
  const [selectedFile, setSelectedFile] = useState(null);
  const [confirmOpen, setConfirmOpen] = useState(false);
  const [pendingFile, setPendingFile] = useState(null);

  const handleDownloadFormat = () => {
    const link = document.createElement("a");
    link.href = "/sample-format.xlsx";
    link.download = "Bulk_Payout_Format.xlsx";
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  // const handleFileUpload = async (e) => {
  //   const file = e.target.files[0];
  //   if (!file) return;

  //   // Reset previous values
  //   setUploadResult(null);
  //   setSelectedFile(null);

  //   setSelectedFile(file);
  //   setUploading(true);

  //   try {
  //     const body = new FormData();
  //     body.append("excelFile", file);
  //     //  let res =  await new Promise((res) => setTimeout(res, 2000)); // mock
  //     let res = await bulkPayout(body);
  //     toastMessage(res?.data?.error, res?.data?.message);

  //     setUploadResult({
  //       success: res?.data?.data?.successCount || 0,
  //       pending: res?.data?.data?.pendingCount || 0,
  //       failed: res?.data?.data?.failureCount || 0,
  //       refund: res?.data?.data?.refundCount || 0,
  //       total: res?.data?.data?.totalRecords || 0,
  //       totalAmount: res?.data?.data?.totalAmount || 0,
  //     });
  //   } catch (err) {
  //     console.error(err);
  //   } finally {
  //     setUploading(false);
  //   }
  // };

  const handleFileUpload = (e) => {
    const file = e.target.files[0];
    if (!file) return;

    const validTypes = [
      "application/vnd.ms-excel", // .xls
      "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet", // .xlsx
    ];

    if (!validTypes.includes(file.type)) {
      toastMessage(true, "Only Excel files (.xls, .xlsx) are allowed.");
      e.target.value = null; // Clear file input
      return;
    }

    // Allow re-upload of same file
    e.target.value = null;

    setPendingFile(file);
    setConfirmOpen(true); // open confirmation dialog
  };

  const proceedUpload = async () => {
    setConfirmOpen(false);
    setUploadResult(null);
    setSelectedFile(pendingFile);
    setUploading(true);

    try {
      const body = new FormData();
      body.append("excelFile", pendingFile);
      //  let res =  await new Promise((res) => setTimeout(res, 4000));
      let res = await bulkPayout(body);
      toastMessage(res?.data?.error, res?.data?.message);

      setUploadResult({
        success: res?.data?.data?.successCount || 0,
        pending: res?.data?.data?.pendingCount || 0,
        failed: res?.data?.data?.failureCount || 0,
        refund: res?.data?.data?.refundCount || 0,
        total: res?.data?.data?.totalRecords || 0,
        totalAmount: res?.data?.data?.totalAmount || 0,
      });
    } catch (err) {
      console.error(err);
    } finally {
      setUploading(false);
      setPendingFile(null);
    }
  };

  return (
    <>
      {uploading && <Loader />}
      <div className="PageHeading">
        <h1>Bulk Payout Service</h1>
      </div>

      <CustomBox>
        {/* Header */}
        {/* <Box display="flex" alignItems="center" gap={2} mb={3}>
       
          <Typography
            variant="h6"
            fontWeight={500}
            sx={{ color: 'var(--bs-nav)', fontSize: 20 }}
          >
            Upload Excel File
          </Typography>
        </Box> */}
        <Box mt={2} textAlign="right">
          <Button
            variant="outlined"
            onClick={handleDownloadFormat}
            sx={{
              color: "var(--bs-primary1)",
              borderColor: "var(--bs-primary1)",
              fontSize: 14,
              textTransform: "none",
              mb: 2,
            }}
          >
            Download Excel Format
          </Button>
        </Box>
        {/* Upload Box */}
        <Box
          sx={{
            position: "relative",
            width: "100%",
            height: 200,
            borderRadius: 2,
            overflow: "hidden",
            bgcolor: "grey.200",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            flexDirection: "column",

            border: "1px solid var(--bs-nav)",
          }}
        >
          <input
            ref={fileInputRef}
            type="file"
            accept=".xls,.xlsx"
            hidden
            onClick={(e) => (e.target.value = null)}
            onChange={handleFileUpload}
          />
          <Button
            variant="outlined"
            sx={{
              color: "#000000",
              borderColor: "#000000",
              fontSize: 14,
              textTransform: "none",
            }}
            disabled={uploading}
            onClick={() => fileInputRef.current.click()}
          >
            Upload Excel File
            {/* {uploading ? <CircularProgress size={20} /> : ''} */}
          </Button>

          {selectedFile && (
            <Typography mt={2} fontSize={13} color="textSecondary">
              Selected File: <strong>{selectedFile.name}</strong>
            </Typography>
          )}
        </Box>

        {/* Download Format */}

        {/* Upload Result Summary */}
        {uploadResult && (
          <Paper
            elevation={0}
            sx={{
              mt: 4,
              p: 3,
              bgcolor: "#f9f9f9",
              borderRadius: 2,
            }}
          >
            <Typography
              variant="subtitle1"
              fontWeight={600}
              sx={{ color: "var(--bs-nav)", fontSize: 16, mb: 2 }}
            >
              Transaction Summary
            </Typography>

            <Stack direction="row" spacing={2} mb={2}>
              <Chip
                label={
                  <Typography
                    sx={{ fontSize: 14, color: "#fff", fontWeight: 500 }}
                  >
                    Success: {uploadResult.success}
                  </Typography>
                }
                sx={{ bgcolor: "green", px: 1.5, py: 0.5 }}
              />
              <Chip
                label={
                  <Typography
                    sx={{ fontSize: 14, color: "#fff", fontWeight: 500 }}
                  >
                    Pending: {uploadResult.pending}
                  </Typography>
                }
                sx={{ bgcolor: "#f0ad4e", px: 1.5, py: 0.5 }}
              />
              <Chip
                label={
                  <Typography
                    sx={{ fontSize: 14, color: "#fff", fontWeight: 500 }}
                  >
                    Failed: {uploadResult.failed}
                  </Typography>
                }
                sx={{ bgcolor: "#d9534f", px: 1.5, py: 0.5 }}
              />
              <Chip
                label={
                  <Typography
                    sx={{ fontSize: 14, color: "#fff", fontWeight: 500 }}
                  >
                    Refund: {uploadResult.refund}
                  </Typography>
                }
                sx={{ bgcolor: "#6f42c1", px: 1.5, py: 0.5 }}
              />
            </Stack>

            <Typography variant="body2" fontSize={14}>
              <strong style={{ color: "var(--bs-nav)" }}>Total Amount:</strong>{" "}
              {uploadResult.totalAmount}
            </Typography>
            <Typography variant="body2" fontSize={14}>
              <strong style={{ color: "var(--bs-nav)" }}>Total Records:</strong>{" "}
              {uploadResult.total}
            </Typography>
          </Paper>
        )}
      </CustomBox>

      <Dialog
        open={confirmOpen}
        onClose={() => setConfirmOpen(false)}
        PaperProps={{
          sx: () => ({
            borderRadius: 4,
            paddingY: 2,
            backdropFilter: "blur(10px)",
            background: "white",
            boxShadow: "0 8px 32px rgba(0, 0, 0, 0.2)",

            //  overflow: tabs?.length === 0 ? 'hidden' : 'auto',
          }),
        }}
      >
        <Box p={3}>
          <Typography
            variant="h6"
            sx={{ color: "var(--bs-nav)", fontSize: "18px" }}
            gutterBottom
          >
            Confirm Bulk Payout
          </Typography>

          <Typography
            variant="body2"
            sx={{ color: "var(--bs-nav)", fontSize: "14px" }}
            mb={1}
          >
            Selected File: <strong>{pendingFile?.name}</strong>
          </Typography>
          <Typography
            variant="body2"
            sx={{ color: "var(--bs-nav)", fontSize: "14px" }}
            mb={2}
          >
            Are you sure you want to proceed with this file? This action may
            trigger financial transactions.
          </Typography>
          <Stack direction="row" spacing={2} justifyContent="flex-end">
            <Button
              onClick={() => setConfirmOpen(false)}
              color="error"
              variant="outlined"
              sx={{
                borderRadius: 2,
                px: 4,
                width: "150px",
              }}
            >
              Cancel
            </Button>
            <Button
              onClick={proceedUpload}
              color="primary"
              variant="contained"
              sx={{
                backgroundColor: "var(--bs-primary1)",
                borderRadius: 2,
                px: 4,
                width: "150px",
              }}
            >
              Proceed
            </Button>
          </Stack>
        </Box>
      </Dialog>
    </>
  );
};

export default BulkPayout;
